.Emulator-canvas {
  position: absolute;
  top: 48px;
  left: 0;
  width: 100vw;
  height: 90vw;
  background: #fff;
  z-index: 0;
}

@media (orientation: landscape) {
  .Emulator-canvas {
    top: 0;
    left: calc(50% - 111.111111vh / 2);
    width: 111.111111vh;
    height: 100vh;
  }
}



