.Dpad {
  position: absolute;
  bottom: 15vh;
  left: 10vw;
  width: 20vmin;
  height: 20vmin;
}

.Dpad > div {
  position: absolute;
  background: red;
}

.Dpad-vertical {
  top: 0;
  left: calc(50% - 33% / 2);
  width: 33%;
  height: 100%;
}

.Dpad-horizontal {
  top: calc(50% - 33% / 2);
  left: 0;
  width: 100%;
  height: 33%;
}



